import React from "react";
import './wrapper.less';

export default function withWrapper(Component) {

  return class WithWrapper extends React.Component {

    state = {isFold: false}

    componentDidMount() {
      this.fold();
    }

    fold = () => {
      if (!this.state.isFold) {
        this.wrapper.classList.add('fold');
      } else {
        this.wrapper.classList.remove('fold');
      }
      this.setState({isFold: !this.state.isFold});
    }

    render() {
      return (
        <div ref={elem => this.wrapper = elem} className="wrapper">
          <Component isFold={this.state.isFold} />
          <button className="fold-btn" onClick={this.fold}></button>
        </div>
      )
    }
  }
}
