import React from 'react';
import withWrapper from '../../hoc/withWrapper';
import './index.less';

function TodoList() {
  const todoList = [
    'Make awesome CSS animation', 
    'Watch awesome bangumi', 
    'Encounter awesome people', 
    'Be an awesome man'
  ];

  return (
    <section className="todo-list">
      <h4>Todo List</h4>
      <main>
        <form>
          <fieldset>
            <legend>My Special Todo List</legend>
            {
              todoList.map(item => (
                <label>
                  <input type="checkbox" />
                  <i className="check"></i>
                  <span>{item}</span>
                </label>
              ))
            }
          </fieldset>
        </form>
      </main>
    </section>
  )
}

export default withWrapper(TodoList);
