import { React, useEffect, useRef } from 'react';
import withWrapper from '../../hoc/withWrapper';
import './index.less';

function FloatTextMenu() {
  const a_list = ['Home', 'Archives', 'Tags', 'Categories', 'About'];

  const splitSentence = () => {
    const menuLinks = ulElem.current.querySelectorAll('a');
    menuLinks.forEach(link => {
      let letters = link.textContent.split('');
      link.textContent = '';
      letters.forEach((letter, index) => {
        const span = document.createElement('span');
        span.textContent = letter;
        span.dataset.text = letter;
        span.style.transitionDelay = `${index*0.05}s`;
        link.appendChild(span);
      });
    });
  }

  const ulElem = useRef();
  useEffect(splitSentence, []);

  return (
    <section className="float-text-menu">
      <h4>Float Text Menu</h4>
      <main>
        <ul ref={ulElem}>
          {
            // eslint-disable-next-line
            a_list.map(a => <li key={a}><a data-text={a}>{a}</a></li>)
          }
        </ul>
      </main>
    </section>
  )
}

export default withWrapper(FloatTextMenu)
